"use client"

import { useState } from 'react'
import Layout from '@/components/shared/Layout'
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Progress } from "@/components/ui/progress"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"

interface IoTSensor {
  id: string
  name: string
  type: 'temperature' | 'humidity' | 'motion' | 'pressure' | 'proximity'
  location: string
  status: 'active' | 'inactive' | 'maintenance'
  value: number
  unit: string
  battery: number
  lastUpdate: string
}

interface SensorZone {
  id: string
  name: string
  sensorCount: number
  activeCount: number
  alertCount: number
}

export default function IoTIntegrationPage() {
  const [selectedZone, setSelectedZone] = useState<string>('all')
  const [selectedType, setSelectedType] = useState<string>('all')

  const sensors: IoTSensor[] = [
    {
      id: "SNR-001",
      name: "Loading Bay Temp",
      type: "temperature",
      location: "Loading Bay A",
      status: "active",
      value: 24.5,
      unit: "Â°C",
      battery: 85,
      lastUpdate: "2 minutes ago"
    },
    {
      id: "SNR-002",
      name: "Storage Humidity",
      type: "humidity",
      location: "Storage Area",
      status: "active",
      value: 45,
      unit: "%",
      battery: 92,
      lastUpdate: "1 minute ago"
    },
    {
      id: "SNR-003",
      name: "Gate Motion",
      type: "motion",
      location: "Main Gate",
      status: "active",
      value: 1,
      unit: "motion/min",
      battery: 78,
      lastUpdate: "30 seconds ago"
    },
    {
      id: "SNR-004",
      name: "Tank Pressure",
      type: "pressure",
      location: "Tank Farm",
      status: "maintenance",
      value: 2.4,
      unit: "bar",
      battery: 45,
      lastUpdate: "15 minutes ago"
    }
  ]

  const zones: SensorZone[] = [
    {
      id: "ZONE-A",
      name: "Loading Bay",
      sensorCount: 12,
      activeCount: 11,
      alertCount: 1
    },
    {
      id: "ZONE-B",
      name: "Storage Area",
      sensorCount: 8,
      activeCount: 8,
      alertCount: 0
    },
    {
      id: "ZONE-C",
      name: "Main Gate",
      sensorCount: 6,
      activeCount: 5,
      alertCount: 2
    }
  ]

  const getStatusBadge = (status: IoTSensor['status']) => {
    const styles = {
      active: 'bg-green-100 text-green-800',
      inactive: 'bg-red-100 text-red-800',
      maintenance: 'bg-yellow-100 text-yellow-800'
    }
    return styles[status]
  }

  const getBatteryColor = (level: number) => {
    if (level > 70) return 'text-green-600'
    if (level > 30) return 'text-yellow-600'
    return 'text-red-600'
  }

  return (
    <Layout title="IoT Sensor Integration">
      <div className="space-y-6">
        {/* Overview Stats */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          <Card className="p-4">
            <h3 className="text-sm font-medium text-gray-500">Total Sensors</h3>
            <p className="text-2xl font-bold">{sensors.length}</p>
          </Card>
          <Card className="p-4">
            <h3 className="text-sm font-medium text-gray-500">Active Sensors</h3>
            <p className="text-2xl font-bold text-green-600">
              {sensors.filter(s => s.status === 'active').length}
            </p>
          </Card>
          <Card className="p-4">
            <h3 className="text-sm font-medium text-gray-500">Battery Critical</h3>
            <p className="text-2xl font-bold text-red-600">
              {sensors.filter(s => s.battery < 20).length}
            </p>
          </Card>
          <Card className="p-4">
            <h3 className="text-sm font-medium text-gray-500">Alerts</h3>
            <p className="text-2xl font-bold text-yellow-600">
              {zones.reduce((acc, zone) => acc + zone.alertCount, 0)}
            </p>
          </Card>
        </div>

        {/* Zone Overview */}
        <Card className="p-6">
          <h2 className="text-xl font-semibold mb-4">Zone Overview</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {zones.map((zone) => (
              <Card key={zone.id} className="p-4">
                <div className="space-y-4">
                  <div className="flex justify-between items-start">
                    <div>
                      <h3 className="font-medium">{zone.name}</h3>
                      <p className="text-sm text-gray-500">{zone.sensorCount} Sensors</p>
                    </div>
                    {zone.alertCount > 0 && (
                      <Badge variant="destructive">
                        {zone.alertCount} Alert{zone.alertCount > 1 ? 's' : ''}
                      </Badge>
                    )}
                  </div>
                  <Progress 
                    value={(zone.activeCount / zone.sensorCount) * 100} 
                  />
                  <div className="text-sm text-gray-600">
                    {zone.activeCount} of {zone.sensorCount} sensors active
                  </div>
                </div>
              </Card>
            ))}
          </div>
        </Card>

        {/* Sensor List */}
        <Card className="p-6">
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-xl font-semibold">Sensor Details</h2>
            <div className="flex space-x-4">
              <Select value={selectedZone} onValueChange={setSelectedZone}>
                <SelectTrigger className="w-[180px]">
                  <SelectValue placeholder="Filter by zone" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All Zones</SelectItem>
                  {zones.map((zone) => (
                    <SelectItem key={zone.id} value={zone.id}>
                      {zone.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
              <Select value={selectedType} onValueChange={setSelectedType}>
                <SelectTrigger className="w-[180px]">
                  <SelectValue placeholder="Filter by type" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All Types</SelectItem>
                  <SelectItem value="temperature">Temperature</SelectItem>
                  <SelectItem value="humidity">Humidity</SelectItem>
                  <SelectItem value="motion">Motion</SelectItem>
                  <SelectItem value="pressure">Pressure</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {sensors.map((sensor) => (
              <Card key={sensor.id} className="p-4">
                <div className="space-y-4">
                  <div className="flex justify-between items-start">
                    <div>
                      <h3 className="font-medium">{sensor.name}</h3>
                      <p className="text-sm text-gray-500">{sensor.location}</p>
                    </div>
                    <Badge className={getStatusBadge(sensor.status)}>
                      {sensor.status.charAt(0).toUpperCase() + sensor.status.slice(1)}
                    </Badge>
                  </div>

                  <div className="flex justify-between items-center">
                    <div>
                      <div className="text-2xl font-bold">
                        {sensor.value}{sensor.unit}
                      </div>
                      <div className="text-sm text-gray-500">
                        Last update: {sensor.lastUpdate}
                      </div>
                    </div>
                    <div className={`text-right ${getBatteryColor(sensor.battery)}`}>
                      <div className="text-lg font-bold">{sensor.battery}%</div>
                      <div className="text-sm">Battery</div>
                    </div>
                  </div>

                  <div className="flex space-x-2">
                    <Button variant="outline" size="sm" className="flex-1">
                      History
                    </Button>
                    <Button variant="outline" size="sm" className="flex-1">
                      Configure
                    </Button>
                  </div>
                </div>
              </Card>
            ))}
          </div>
        </Card>

        {/* Data Visualization */}
        <Card className="p-6">
          <h2 className="text-xl font-semibold mb-4">Real-time Analytics</h2>
          <div className="bg-gray-100 h-[300px] rounded-lg flex items-center justify-center">
            <p className="text-gray-500">Real-time sensor data visualization would be displayed here</p>
          </div>
        </Card>
      </div>
    </Layout>
  )
}
